import React from 'react'
import '../Styles/Dudas.css'
export default function Dudas() {
    return (
        <div className='dudas'>
            <div className='dudaresp'>
                <img src={require('../Assets/dudas.png')} className='img-fluid'/>
                <div className='pregunt'>
                <h1>Como Realizo una compra?</h1>
                <h2>Pick Shop no realiza ninguna venta. unicamente muestra los precios de los principales supermercados del per, para que puedas ahorrar en tu siguiente compra. Si deseas adquirir el producto que viste en Pick Shop solo tienes que hacer click en la tienda de tu preferencia.</h2>
                <h1>Como agrego un producto a la lista?</h1>
                <h2>Unicamente tienes que seleccionar el producto de tu preferencia y en la vista detallada te mostrara un boton que dice agregar a la lista</h2>
                <h1>El Precio mostrado no coincide?</h1>
                <h2>Existe la posibilidad que los precios no coincidan, ya que los precios que se manejan en la pagina web se actualizan cada cierto tiempo.</h2>
                <h1>El producto esta defectuoso?</h1>
                <h2>Nosotros no nos hacemos cargo si algun producto se encuentra daado o en mal estado ya que nosotros no concretamos la venta de dicho producto.</h2>
                </div>
            </div>
        </div>
    )
}
