import React from 'react'
import Carousel from 'react-bootstrap/Carousel';
import { Link, NavLink } from 'react-router-dom';
import '../Styles/Carousel.css';
export default function carousel() {
    return (
        <div className='home' >
            {/* -------------------------Carousel-------------------------- */}
                <Carousel>
                    <Carousel.Item interval={1700}>
                        <img
                        className="d-block w-100"
                        src={require('../Assets/carousel1.png')}
                        alt="First slide"
                        />
                        <Carousel.Caption>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item interval={1700}>
                        <img
                        className="d-block w-100"
                        src={require('../Assets/carousel2.png')}
                        alt="Second slide"
                        />
                        <Carousel.Caption>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item interval={1700}>
                        <img
                        className="d-block w-100"
                        src={require('../Assets/carousel3.png')}
                        alt="Third slide"
                        />
                        <Carousel.Caption>
                        </Carousel.Caption>
                    </Carousel.Item>
                </Carousel>
            
        {/* ---------------------------supermercados---------------------------- */}
        <div className='supermercados'>
            <div className='title-supermercados'>Supermercados</div>
        </div>
        <div className='super'>
        <div className="parent-super">
                <div className="title-metro">
                    <div className='card-super'>
                        <img src={require('../Assets/metro.png')} alt=''/>
                        <div>METRO</div>
                    </div>
                </div>
                <div className="title-tottus">
                    <div className='card-super'>
                        <img src={require('../Assets/tottus.png')} alt=''/>
                        <div>TOTTUS</div>
                    </div>
                </div>
                <div className="title-realplaza">
                    <div className='card-super'>
                        <img src={require('../Assets/realplaza.png')} alt=''/>
                        <div>REAL PLAZA</div>
                    </div>
                </div>
                <div className="title-plazavea">
                    <div className='card-super'>
                        <img src={require('../Assets/plazavea.png')} alt=''/>
                        <div> PLAZA VEA</div>
                    </div>
                </div>
            </div>
        </div>
        {/* Quienes somos? */}
        <div className='banner-cont'>
            <div className='tittle-banner'>Quienes somos?</div>
            <div className='logo-banner'>PICKSHOP</div>
            <div className='text-banner'>Somos un grupo de estudiantes peruanos que nos propusimos desarrollar este proyecto que permite comparar precios de productos de varias cadenas de supermercados, adems permite realizar la identificacin y seleccin de los ya mencionados de forma rapida y sencilla.</div>
        </div>
        {/* banner */}
        <div className='banner-content'>
            <div className="parent-banner" id='categoria'>
            
                <div className="banner1">
                <Link to='/categoria/tecnologia'>
                        <div className='card-banner' >
                            <img className='tecnologia-banner' src={require('../Assets/Tecnologa.png')} alt=''></img>
                        </div>
                    </Link>
                </div>
                <div className="banner2">
                <Link to='/categoria/Electrohogar'>
                    <div className='card-banner'>
                        <img className='electrodomesticos-banner' src={require('../Assets/electrodomesticos.png')} alt=''></img>
                    </div>
                </Link>
                </div>
                <div className="banner3">
                <Link to='/categoria/Moda'>
                <div className='card-banner'>
                    <img className='ropaycalzado-banner' src={require('../Assets/ropaycalzado.png')} alt=''></img>
                </div>
                </Link>
                </div>
                <div className="banner4">
                <Link to='/categoria/Frutas y Verduras'>
                <div className='card-banner'>
                    <img className='frutas-banner' src={require('../Assets/Frutasyverduras.png')} alt=''></img>
                </div>
                </Link>
                </div>
            </div>
        </div>
        </div>
    )
}
