import React from 'react'
import { AiOutlineCloseCircle, AiTwotoneDelete } from "react-icons/ai";
import '../Styles/Carrito.css'
export default function carrito() {
    return (
        <div className='carritos '>
            <div className='carrito '>
            <div className='carritoCls'>
                <div className='iconcls'><AiOutlineCloseCircle/></div>
                <h3> Lista de Productos Seleccionados:</h3>
            </div>
            
            <div className='carritocenter'>
                <div className='carrito-item'>
                    
                    <img src={require('../Assets/tottus.png')}></img>
                    <div>
                        <h3>title</h3>
                        <h4>modelo</h4>
                        <h4>precio</h4>
                        <p className='preciocarri'></p>
                    </div>
                    <div>
                        <div className='deleteicon'><AiTwotoneDelete/></div>
                    </div>
                </div>
            </div>
            <div className='carritofooter'>
                <h3>Total: </h3>
            </div>
            </div>
        </div>
    )
}
