import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import algoliasearch from 'algoliasearch/lite'
import '../Styles/Detalleproducto.css'
import locationcenter from './localitation.json'
import { GoogleMap, Marker, useLoadScript } from '@react-google-maps/api'
const APPLICATION_ID = 'K1WJAZ3O9C'
const SEARCH_API_KEY = '88e31bfe361350222c3a570f4b7ad298'
const ALGOLIA_INDEX = 'pro_scraping_market'
const client = algoliasearch(APPLICATION_ID, SEARCH_API_KEY)
const index = client.initIndex(ALGOLIA_INDEX)
export default function Detalleproducto() {
	const [resultpro, setResultpro] = useState([])
	const [center, setCenter] = useState([])
	const [DataNav, setDataNav] = useState([])

	const { key } = useParams()

	const { isLoaded } = useLoadScript({
		googleMapsApiKey: 'AIzaSyD4lg_412Kw3K-JImtLf2RjmMjmqnEDlak'
	})

	function getCoordinates() {
		return new Promise(function (resolve, reject) {
			navigator.geolocation.getCurrentPosition(resolve, reject)
		})
	}

	const asyncFilter = async (arr, predicate) => {
		const results = await Promise.all(arr.map(predicate))

		return arr.filter((_v, index) => results[index])
	}

	const Navgs = async () => {
		const position = await getCoordinates()
		const datosubi = {
			lat: position.coords.latitude,
			lng: position.coords.longitude
		}

		const localitations = await Promise.all(
			locationcenter.map((localitation) => ({
				lat: localitation.lat,
				lng: localitation.lng,
				supermercado: localitation.supermercado
			}))
		)
		setCenter(datosubi)
		setDataNav(localitations)
	}

	const InitData = async () => {
		const resq = await index.search(key)
		const _resultpro = await Promise.all(
			resq.hits.map((hit) => ({
				key: hit.key,
				marca: hit.marca,
				nombre: hit.nombre,
				modelo: hit.marca,
				tienda: hit.tienda,
				precio: hit.precio,
				imagen: hit.imagen,
				referent: hit.href,
			}))
		)
		setResultpro(_resultpro[0])
		Navgs()
	}
	useEffect(() => {
		InitData()
	}, [key])

	if (!isLoaded) return <div>Loading...</div>
	if (DataNav.length === 0) return <div>Loading...</div>
	if (center.length === 0) return <div>Loading...</div>

	return (
		<div>
			<div>
				{resultpro.length === 0 ? (
					<h1></h1>
				) : (
					<div className="fondodetail">
						<div className="detail-right">
							<img
								className="img-fluid img-thumbnail"
								src={resultpro.imagen}
								alt=""
							/>
						</div>
						<div className="detail-left">
							<div className="title-detalle">{resultpro.nombre}</div>
							<div className="tiendaprec">
								Producto Distribuido {resultpro.tienda}
							</div>
							<ul className="cont-caract">
								<div className="caracte">Modelo : {resultpro.modelo}</div>
								
							</ul>
							<div className="tiendaprec">Precio : {resultpro.precio}</div>
							
							<div className="select-agg">
								<a href={resultpro.referent}><button>Comprar {resultpro.tienda}</button></a>
								
							</div>
						</div>
					</div>
				)}
				{/* <h1>{center.lat}</h1> */}
				<div className="geolocalizacion">
					<div className="mapa">
						<h3>Encuentra el supermercado mas cercano</h3>
						<GoogleMap
							zoom={14}
							center={center}
							mapContainerClassName="map-container"
						>
							<Marker position={center} />
							{DataNav.map(
								(item, j) =>
									item.supermercado === resultpro.tienda ? (
										<Marker
											key={j}
											icon={{
												url:  require(`../Assets/${item.supermercado.split(' ').join('').toString().toLowerCase()}.png`)
											}}
											position={{
												lat: parseFloat(item.lat),
												lng: parseFloat(item.lng)
											}}
										/>
									) : ''
							)}
						</GoogleMap>
					</div>
				</div>
			</div>
		</div>
	)
}