import React from 'react';
import Navbar from "./components/Navbar";
import Carousel from "./components/Carousel";
import Nosotros from './components/Nosotros';
import Contactanos from './components/Contactanos'
import Detalleproducto from './components/Detalleproducto';
import PiePagina from './components/PiePagina';
import Carrito from './components/Carrito';
import {
  BrowserRouter,
  Route,
  Routes,
  HashRouter,
} from "react-router-dom";
import Dudas from './components/Dudas';
import './App.css';
import ListaProducto from './components/ListaProducto';
import Listacategoria from './components/ListaCategoria';
function App() {
	return (
    <div className='pagina'>
      <HashRouter>
      <Navbar/>
      <Carrito/>
      <Routes>
      <Route path='/' element={
            <>
            
            <Carousel/>
            
            </>
          }/>
      <Route path='/Nosotros' element={
            <>
            
            <Nosotros/>
            
            </>
          }/>
      <Route path='/Contactanos' element={
            <>
            
            <Contactanos/>
            </>
          }/>
      <Route path='/Dudas' element={
            <>
            <Dudas/>
            </>
          }/>
      {/* detalleproducto */}
      <Route path="/producto/:key" element= {
        <Detalleproducto/>
      }></Route>
      {/* ----------------- */}
      <Route path="/buscar/:enviardirecci" element={
        <ListaProducto/>
      }>
      </Route>
      <Route path="/categoria/:categoria" element={
        <Listacategoria/>
      }>
      </Route>
      <Route path='*'  element= {
        <>
        <div className='cont'>
        <img className="img-fluid" src={require('./Assets/error2.png')} alt=''></img>
        </div>
        </>
      }></Route>
      </Routes>
      <PiePagina/>
    </HashRouter>
    </div>
    
	);
}

export default App;
