import React from 'react'
import '../Styles/Nosotros.css';
export default function Nosotros() {
    return (
        <div className='nosotros'>
            <img className='img-fluid' src={require('../Assets/nosotros.png')} alt=""></img>
            <div className='nosotrostext'>
                <h1 className='title-nosotros'>Nosotros</h1>
                <p>Hola a todos, somos alumnos de la Universidad Cesar Vallejo y desde siempre nos a gustado apoyar a la comunidad, ya sea compartiendo nuestros conocimientos aprendidos y apoyando en los momentos ms difciles. Sin dudas, ahora es uno de esos momentos; por ello, en plena cuarentena decidimos abordarnos en la creacion de PickShop con el principal objetivo de permitir ahorrar tiempo y dinero a ms peruanos en sus compras online al comparar los precios de miles de productos electrnicos de las tiendas ms importantes del Per. </p>
            </div>
        </div>
    )
}
