import React, { useEffect, useState } from 'react'
import algoliasearch from 'algoliasearch/lite'
import { NavLink, useParams } from 'react-router-dom'
import '../Styles/ListaProducto.css'
const APPLICATION_ID = 'K1WJAZ3O9C'
const SEARCH_API_KEY = '88e31bfe361350222c3a570f4b7ad298'
const ALGOLIA_INDEX = 'pro_scraping_market'
const client = algoliasearch(APPLICATION_ID, SEARCH_API_KEY)
const index = client.initIndex(ALGOLIA_INDEX)
export default function ListaProducto() {
    const {enviardirecci} = useParams()
    const [listarpro, setListarpro] = useState([])
    const resultadosbusqueda = async () => {
        const respues = await index.search(enviardirecci)
        const respuestalis = await Promise.all(
            respues.hits.map((hit)=>({
                key: hit.objectID,
                nombre: hit.nombre,
                marca: hit.marca,
                imagen: hit.imagen,
                tienda: hit.tienda,
                precio: hit.precio,
            }) )
        )
        setListarpro(respuestalis)
    }
    
    console.log(listarpro)
    console.log('Detente')
    useEffect(()=> {
        resultadosbusqueda()
    },  [enviardirecci])
    if (listarpro.length === 0) return <div>Loading...</div>
    return (
        <div>
            {listarpro.length === 0 
            ? (<div>No hay resultados de busqueda</div>)
            : (
                <div className='connt-resul'> 
                    {listarpro.map((resultado)=>
                        {const{key} = resultado
                        return(
                            <NavLink to={`/producto/${key}`} className='resullink'>
                                <div className='resultado-card' key={resultado.id}>
                                <img src={resultado.imagen} alt="imagen no encontrada"/>
                                <div className='marcaresult' >{resultado.marca}</div>
                                <div className='nombreresul'>{resultado.nombre}</div>
                                <div className='tiendaresul'>{resultado.tienda}</div>
                                <h4 className='precioresul'>{resultado.precio}</h4>
                            </div>
                            </NavLink>
                            
                        )}
                    )} 
                </div>
                
            )
        }
        </div>
    )
}
