import React, { useState } from 'react'
import '../Styles/Navbar.css'
import { FiSearch } from 'react-icons/fi'
import { HiOutlineShoppingBag, HiMenu, HiOutlineX } from 'react-icons/hi'
import { NavLink, useNavigate } from 'react-router-dom'
import { motion } from 'framer-motion'
import algoliasearch from 'algoliasearch/lite'
const APPLICATION_ID = 'K1WJAZ3O9C'
const SEARCH_API_KEY = '88e31bfe361350222c3a570f4b7ad298'
const ALGOLIA_INDEX = 'pro_scraping_market'
const client = algoliasearch(APPLICATION_ID, SEARCH_API_KEY)
const index = client.initIndex(ALGOLIA_INDEX)
export default function Navbar() {
	const [navisActive, setNavisActive] = useState(false)

	const variants = {
		open: { opacity: 1, y: 0 },
		closed: { opacity: 0, y: '-100%' }
	}
	const navigate = useNavigate()
	const [show, setShow] = useState(false)
	const [results, setResults] = useState(null)
	const [updated, setUpdated] = useState('')
	const handleKeyDown = (event) => {
		if (event.key === 'Enter') {
			const enviardirecci = event.target.value
			navigate(`/buscar/${enviardirecci}`)
			setUpdated(enviardirecci)
			/* navigate(`/buscar/${updated}`) */
		}
	}
	console.log(updated)
	const performSearch = async (value) => {
		const { hits } = await index.search(value, {
			hitsPerPage: 6
		})
		const results = hits.map((hit) => {
			const { objectID: key, href, _highlightResult } = hit
			const {
				marca: { value: marca }
			} = _highlightResult
			const {
				nombre: { value: nombre }
			} = _highlightResult
			const {
				imagen: {value: imagen}
			}	= _highlightResult
			
			return { key, href, marca, nombre, imagen }
		})
		setResults(results)
		console.log(results)
	}
	const handleChange = (e) => {
		const { value } = e.target
		console.log(navisActive)
		setNavisActive(true)
		console.log(navisActive)
		value === '' ? setResults(null) : performSearch(value)
	}

	const handleBur = (e) => {
		setTimeout(() => {
			setNavisActive(false)
		}, 100)
	}

	return (
		<header className="navv">
			<div className="menu-left">
				<NavLink to={'/'} key={''} className="logo">
					PICKSHOP
				</NavLink>
				<ul className="navbarlist">
					<li>
						<a href='#categoria'className="navbar-item">Categoria</a>
					</li>
					<li>
						<NavLink className="navbar-item" to={'/Nosotros'} key={'Nosotros'}>
							Nosotros
						</NavLink>
					</li>
					<li>
						
					</li>
					<li>
						<NavLink className="navbar-item" to={'/Dudas'} key={'Dudas'}>
							Dudas?
						</NavLink>
					</li>
				</ul>
			</div>
			<div className="menu-right">
				<div className="search-cont">
					<span className="iconsearch">
						<FiSearch />
					</span>
					<input
						type="text"
						className="search"
						onBlur={handleBur}
						onKeyDown={handleKeyDown}
						onChange={handleChange}
						placeholder="Buscar Producto"
					></input>
					{!navisActive ? (
						''
					) : results === null ? (
						<div className="ais-Hits">
							<ul className="resultados ">
								Producto no Encontrado
								</ul>
								</div>

					) : (
						<div className="ais-Hits">
							<ul className="resultados ">
								{results.map((result) => {
									const { key, href, marca, nombre, imagen } = result
									return (
										<div className="resultadosss" key={key}>
											<NavLink className='navmio'
												to={`/producto/${key}`}
												href={href}
												onClick={() => console.log()}
											>
												<img src={imagen} alt=""/>
												<div
													className="resultadosss-modelo"
													dangerouslySetInnerHTML={{ __html: nombre }}
												/>
												<div
													className="resultadosss-nombre"
													dangerouslySetInnerHTML={{ __html: marca }}
												/>
											</NavLink>
										</div>
									)
								})}
							</ul>
						</div>
					)}
				</div>
				<div className="use-list">
					
				</div>
				<div className="side">
					<motion.nav
						className="sider-nav"
						animate={show ? 'open' : 'closed'}
						variants={variants}
						transition={{ duration: 0.5 }}
					>
						<motion.div className="inner-var">
							<ul className="siderlist">
								<li>
									<NavLink className="sider-item">Categoria</NavLink>
								</li>
								<li>
									<NavLink
										className="sider-item"
										to={'/Nosotros'}
										key={'Nosotros'}
									>
										Nosotros
									</NavLink>
								</li>
								<li>
									<NavLink className="sider-item" to={'/Dudas'} key={'Dudas'}>
										Dudas?
									</NavLink>
								</li>
							</ul>
						</motion.div>
					</motion.nav>
					<motion.button
						className="toogle"
						onClick={() => setShow((show) => !show)}
						whileHover={{ scale: 1.1 }}
						whileTap={{ scale: 0.9 }}
					>
						{show ? (
							<div className="sider-icon">
								<HiOutlineX />
							</div>
						) : (
							<div className="sider-icon">
								<HiMenu />
							</div>
						)}
					</motion.button>
				</div>
			</div>
		</header>
	)
}